import React, { useContext } from 'react'
import PropTypes from 'prop-types'
import { M1FContext } from '../../Context'
import useClickOutside from '../../useClickOutside'

const ModalContainer = ({ children, nullValue }) => {
  const { modalToggle } = useContext(M1FContext)
  const domNode = useClickOutside(() => {
    modalToggle(false)
    nullValue(null)
  })
  return (
    <div className="tokyo_tm_modalbox opened">
      <div className="box_inner" ref={domNode}>
        <div className="close">
          <a
            href="#"
            onClick={(e) => {
              e.preventDefault()
              modalToggle(false)
              nullValue(null)
            }}
          >
            <i className="icon-cancel" />
          </a>
        </div>
        <div className="description_wrap">{children}</div>
      </div>
    </div>
  )
}

ModalContainer.propTypes = {
  children: PropTypes.node.isRequired,
  nullValue: PropTypes.func.isRequired
}

export default ModalContainer
