import React, { useContext } from 'react'
import { M1FContext } from '../../Context'
import ModalContainer from './ModalContainer'

const ServiceModal = () => {
  const { serviceModal, setServiceModal } = useContext(M1FContext)
  return (
    <ModalContainer nullValue={setServiceModal}>
      <div className="service_popup_informations float-left clear-both h-auto w-full">
        <div className="image border-[#34353A] bg-[#ffffff]">
          <img src={serviceModal.image} alt="" />
          <div
            className="main"
            data-img-url={serviceModal.image}
            style={{ backgroundImage: `url(${serviceModal.image})` }}
          />
        </div>
        <div className="main_title">
          <h3>{serviceModal.name}</h3>
        </div>
        <div className="descriptions float-left w-full">
          {serviceModal.text.map((text, i) => (
            <p
              className={serviceModal.text.length - 1 === i ? '' : 'mb-[15px]'}
              key={i}
            >
              {text}
            </p>
          ))}
        </div>
      </div>
    </ModalContainer>
  )
}
export default ServiceModal
