import React, { useContext, useState } from 'react'
import { Link } from 'gatsby'
import { StaticImage } from 'gatsby-plugin-image'
import { M1FContext } from '../Context'

const Mobile = () => {
  const [toggle, setToggle] = useState(false)
  const { navChange, nav, menus } = useContext(M1FContext)
  return (
    <>
      <div className="tokyo_tm_topbar fixed left-0 right-0 top-0 z-[14] hidden h-[50px] bg-white">
        <div className="topbar_inner clear-both flex h-full w-full items-center justify-between px-[20px] py-0">
          <div className="logo">
            <Link to={'/'} alt={'logo'} width={32}>
              <h3 className="font-poppins font-white text-[25px] uppercase tracking-[4px]">
                <StaticImage
                  className={'mr-5'}
                  src={'../images/logo.svg'}
                  alt={'The Vault Logo'}
                  width={24}
                />
                Vault
              </h3>
            </Link>
          </div>
          <div className="trigger relative top-[5px]">
            <div
              className={`hamburger hamburger--slider ${
                toggle ? 'is-active' : ''
              }`}
              onClick={() => setToggle(!toggle)}
            >
              <div className="hamburger-box w-[30px]">
                <div className="hamburger-inner" />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div
        className={`tokyo_tm_mobile_menu fixed right-[-200px] top-[50px] z-[15] h-[100vh] w-[200px] bg-white transition-all duration-300 ${
          toggle ? 'opened' : ''
        }`}
      >
        <div className="menu_list float-left clear-both h-auto w-full px-[20px] pb-[0px] pt-[100px] text-right">
          <ul className="transition_link list-none">
            {menus.map((menu) => (
              <li
                className={`active mb-[7px] ${
                  menu.href === nav ? 'active' : ''
                }`}
                key={menu.id}
              >
                <Link
                  className={'font-montserrat text-black'}
                  onClick={() => {
                    navChange(menu.href)
                    setToggle(!toggle)
                  }}
                  to={`/${menu.href}`}
                >
                  {menu.name}
                </Link>
              </li>
            ))}
          </ul>
        </div>
      </div>
    </>
  )
}
export default Mobile
