import React, { useContext, useEffect } from 'react'
import PropTypes from 'prop-types'
import { useLocation } from '@reach/router'
import { M1FContext } from '../Context'
import DetailsModal from '../components/popup/DetailsModal'
import Mobile from './Mobile'
import Sidebar from './Sidebar'
import NewsModal from '../components/popup/NewsModal'
import ServiceModal from '../components/popup/ServiceModal'
import { trimSlashes } from '../utils'

const Layout = (props) => {
  const {
    modal,
    serviceModal,
    newsModal,
    portfolioDetailsModal,
    navChange,
    nav
  } = useContext(M1FContext)

  const location = useLocation()
  useEffect(() => {
    const currentNav = trimSlashes(location.pathname) || ''
    if (currentNav !== nav) {
      navChange(currentNav)
    }
  }, [location])

  return (
    <>
      {modal && serviceModal && <ServiceModal />}
      {modal && newsModal && <NewsModal />}
      {modal && portfolioDetailsModal && <DetailsModal />}
      <div className="tokyo_tm_all_wrap">
        <Mobile />
        <Sidebar />
        <main className="rightpart relative float-left min-h-[100vh] w-full bg-[#f8f8f8] pl-[450px]">
          <div className="rightpart_in relative float-left clear-both min-h-[100vh] w-full border-l border-solid border-[#ebebeb]">
            {props.children}
          </div>
        </main>
      </div>
    </>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired
}

export default Layout
